    },
    orderBillButton(row) {
      this.billAlert = row
      this.dialogFormVisible = true
    },
    cancelOrderBillButton() {
      this.dialogFormVisible = false
      this.billAlert = []
    },
    orderBillPost() {
      let items = this.billAlert
      let allUrl = this.allUrl
      let _this = this
      console.log(items)
      items.billDate = items.billDateStr
      items.billDateStr = ''


      this.$confirm('此操作将添加发票信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          $.ajax({
            type:"POST",
            url: allUrl+'/updateOrderBill',
            data:JSON.stringify(items),
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            //dataType: "JSON",
            success(result) {
              _this.loadUserOrder();
              _this.dialogFormVisible = false
              console.log(result);
            },
            error(result) {
              console.log(result.message)
            }
          });
          this.$message({
            type: 'success',
            message: '添加成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消添加'
          });
        });

    },
    orderAfterBillPost() {
      let items = this.billAfterAlert
      let mergeUrl = this.mergeUrl
