          });
        });

    },

    postPayMethods(item) {
      let payWay = this.payWay;
      let payNumber = this.payNumber;
      let allUrl = this.allUrl;
      if(!payWay.length ) {
        alert('支付方式不能为空！')
        return
      }
      if(!payNumber.length){
        alert('付款流水号不能为空')
        return
      }
      if(payWay == '亿佰公账') payWay = 0;
      if(payWay == '亿佰支付宝') payWay = 1;
      if(payWay == '坤刻支付宝') payWay = 2;
      if(payWay == '陈晓璇银行卡') payWay = 3;
      if(payWay == '刘嘉诚银行卡') payWay = 4;
      if(payWay == '刘晓华银行卡') payWay = 5;
      if(payWay == '陈晓璇支付宝') payWay = 6;
      if(payWay == '刘嘉诚支付宝') payWay = 7;
      if(payWay == '刘晓华支付宝') payWay = 8;
      item.payWay =  payWay;
      item.payNumber = payNumber;

      $.ajax({
        type:"POST",
        url: allUrl+'/updateOrderStatus',
        data:JSON.stringify(item),
        headers: {
          'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
          'Access-Control-Allow-Origin': '*'
        },
