    },
    listenPageProOrder(data) {
      this.orderProItems = data;
    },
    getPayWay(status){
      if (status == '0') return '库存'
      if (status == '1') return '亿佰公账'
      if (status == '2') return '亿佰支付宝'
      if (status == '3') return '坤刻支付宝'
      if (status == '4') return '陈晓璇银行卡'
      if (status == '5') return '刘嘉诚银行卡'
      if (status == '6') return '刘晓华银行卡'
      if (status == '7') return '陈晓璇支付宝'
      if (status == '8') return '刘嘉诚支付宝'
      if (status == '9') return '刘晓华支付宝'
    },
    getUserOrder() {
      let allUrl = this.allUrl;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url: allUrl+'/getFinanceOrder?page=0',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    getUserMonthOrder() {
      let proUrl = this.proUrl;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url: proUrl+'/orderMerge/getAll/1/0',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    getProviderOrder() {
      let proUrl = this.proUrl;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url: proUrl+'/resource/getAllWithNoJudy/1/0',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    loadProviderOrder() {
      this.getProviderOrder()
      .then( result => {
        console.log(result)
        this.orderProItems = result.message.list
        this.proTotal = result.message.total
        this.proPageSize = result.message.size
      })
    },
    loadUserMonthOrder() {
      this.getUserMonthOrder()
      .then( result => {
        console.log(result)
        this.orderAfterItems = result.message.list
        this.afterTotal = result.message.total
        this.afterPageSize = result.message.size
      })
    },
    loadUserOrder() {
      this.getUserOrder()
      .then( result => {
        console.log(result);
        this.orderItems = result.message.list;
        this.total = result.message.total;
        this.pageSize = result.message.size;
      })
    },
    getDate(result){
      let date = new Date(result);
      let first = String(date).substring(1, 10);
      let last = String(date).substring(11, 19);
      let newDate = first+' '+last;
      return date;
    },
    getItemType(status) {
      if(status == 'hotel') return '酒店';
      if(status == 'flight') return '机票';
      if(status == 'tool') return '旅游用品';
      if(status == 'other') return '其他';
      if(status == 'visa') return '签证';
      if(status == 'car') return '用车';
      if(status == 'flighotel') return '机加酒';
      if(status == 'oneday') return '一日游';
      if(status == 'gongying') return '供应商结算单';
    },
  },
  components: {

    foot
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

.row > div{
  text-align: center;
  border: 0.1px solid rgba(100, 100, 100, 0.1);
}
.row{

  height:50px;
  line-height: 50px;
  margin-top:5px;
}
.payClass{
  height:35px;

  position: relative;
  top:10px;

}
.data_body{
  cursor: pointer;
}
.data_body:hover{
  border: 0.1px solid rgba(100, 100, 100, 0.3);
}
.data_title{
  font-size: 18px;
}
.detail_title{
  background: rgba(100,100, 100, 0.2);
  font-size: 17px;
}
.detail_body_hover{
  border: 0.1px solid rgba(100, 100, 100, 0.3);
}
.orderDate{
  font-size: 12px;
}
.data_body> div{
  overflow:hidden;
  text-overflow : ellipsis;
  white-space : nowrap;
}
.cell{
  text-align: center;
}
.Zcol{
  border: 1px solid rgba(1,1,1,0.2);
  text-align: center;
  font-size: 16px;
  height:40px;
  line-height: 40px;
  border-radius: 3px;
  margin-top:1px;
}
</style>



