<template>
  <div class="reimbursed">

    <!--  按钮页面 -->
    <form method="post" :action="postUrl"  v-show="false">
      <input type="text"  name="startDate" :value="date[0]"></input>
      <input type="text" name="endDate" :value="date[1]"></input>
      <input type="submit" id="postSubmit" style="margin-top:10px;"></input>
    </form>
    <!--  表格页面-->

      <el-tabs v-model="activeName" type="card" @tab-click="cardClick">
      <el-tab-pane label="报销确认" name="first">
        <el-row>

          <el-col :span="2">

          </el-col>
          <el-col :span="22">
            <el-form :inline="true" :model="beforeSearch" class="demo-form-inline">
              <el-form-item>
                <el-button type="success" size="mini" style="margin-left:20px;" @click="postAllPick">审核确认</el-button>
              </el-form-item>
              <el-form-item label="申请人">
                <el-select v-model="beforeSearch.user" filterable placeholder="请选择">
                  <el-option
                    v-for="item in user"

                    :key="item.id"
                    :label="item.nickname"
                    :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="loadSearch" size="mini">查询</el-button>
                <el-button type="danger" @click="searchBack" size="mini">清空</el-button>

              </el-form-item>
              <el-form-item>
                <el-tag>勾选总金额为 {{allTotal}} 元</el-tag>
              </el-form-item>
            </el-form>

          </el-col>
        </el-row>


        <el-table
        class="reimburseTable"
        :data="tableData"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        >
          <el-table-column
            type="selection"
            width="100">
          </el-table-column>
            <el-table-column
              prop="nickName"
              label="申请人"
              sortable
              width="300">
            </el-table-column>

            <el-table-column
              prop="cost"
              label="申请总金额"
              sortable
              width="300"
              >
            </el-table-column>
            <el-table-column
              prop="status"
              label="申请状态"
              width="250"
              :filters="[{ text: '审核中', value: '审核中' }, { text: '已拒绝', value: '已拒绝' }, {text: '已审核', value: '已审核'}]"
              :filter-method="filterTag"
              filter-placement="bottom-end">
              <template slot-scope="scope">
                <el-tag
                  :type="scope.row.status === '未审核' ? 'primary' : 'success'"
                  close-transition>{{scope.row.status}}</el-tag>
              </template>
            </el-table-column>
            <el-table-column label="操作" width="200">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  :disabled="scope.row.status === '已审核' || scope.row.status === '已拒绝'"
                  @click="handleEdit(scope.$index, scope.row)">明细</el-button>
              </template>
            </el-table-column>
          </el-table>
          <!--  foot -->

      </el-tab-pane>
      <el-tab-pane label="报销已确认" name="second">
        <el-row>

          <el-col :span="2">

          </el-col>
          <el-col :span="22">
            <el-form :inline="true" :model="afterSearch" class="demo-form-inline">
              <el-form-item label="申请人">
                <el-select v-model="afterSearch.user" filterable placeholder="请选择">
                  <el-option
                    v-for="item in user"

                    :key="item.id"
                    :label="item.nickname"
                    :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="loadAfterSearch" size="mini">查询</el-button>
                <el-button type="danger" @click="afterSearchBack" size="mini">清空</el-button>
                <el-button type="primary" @click="postDown" size="mini">全员导出</el-button>
              </el-form-item>
            </el-form>
            <el-date-picker
              style="margin-left:20px;"
              v-model="date"
              type="daterange"
              align="right"
              unlink-panels
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :picker-options="pickerOptions2">
            </el-date-picker>
          </el-col>
        </el-row>


        <el-table
        class="reimburseTable"
        :data="tableData2"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        >
          <el-table-column
            type="selection"
            width="100">
          </el-table-column>
            <el-table-column
              prop="nickName"
              label="申请人"
              sortable
              width="300">
            </el-table-column>

            <el-table-column
              prop="cost"
              label="申请总金额"
              sortable
              width="300"
              >
            </el-table-column>
            <el-table-column
              prop="status"
              label="申请状态"
              width="250"
              :filters="[{ text: '审核中', value: '审核中' }, { text: '已拒绝', value: '已拒绝' }, {text: '已审核', value: '已审核'}]"
              :filter-method="filterTag"
              filter-placement="bottom-end">
              <template slot-scope="scope">
                <el-tag
                  :type="scope.row.status === '未审核' ? 'primary' : 'success'"
                  close-transition>{{scope.row.status}}</el-tag>
              </template>
            </el-table-column>
            <el-table-column label="操作" width="200">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  :disabled="scope.row.status === '已审核' || scope.row.status === '已拒绝'"
                  @click="afterHandleEdit(scope.$index, scope.row)">明细</el-button>
              </template>
            </el-table-column>
          </el-table>




      </el-tab-pane>

      <el-dialog title="明细" :visible.sync="dialogFormVisible2" width="80%">
        <el-table :data="orderDetail">
          <el-table-column property="applyTimeStr" label="申请时间" width="150"></el-table-column>
          <el-table-column property="nickName" label="姓名" width="200"></el-table-column>
          <el-table-column property="category" label="类目"></el-table-column>
          <el-table-column property="event" label="事由"></el-table-column>
          <el-table-column property="cost" label="金额"></el-table-column>
          <el-table-column property="status" label="状态"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)">拒绝</el-button>
            </template>
          </el-table-column>
        </el-table>
          <!--
        <foot :total="detailTotal"
              :pageSize = "detailPageSize"
              v-on:say = "listenPagedetailOrder"
              :url = "detailUrl"
        ></foot>
      -->
      </el-dialog>

      <el-dialog title="明细" :visible.sync="dialogFormVisible3" width="80%">
        <el-table :data="orderAfterDetail">
          <el-table-column property="applyTimeStr" label="申请时间" width="150"></el-table-column>
          <el-table-column property="nickName" label="姓名" width="200"></el-table-column>
          <el-table-column property="category" label="类目"></el-table-column>
          <el-table-column property="event" label="事由"></el-table-column>
          <el-table-column property="cost" label="金额"></el-table-column>
          <el-table-column property="status" label="状态"></el-table-column>
        </el-table>
          <!--
        <foot :total="detailAfterTotal"
              :pageSize = "detailAfterPageSize"
              v-on:say = "listenPagedetailOrder"
              :url = "detaiAfterlUrl"

        ></foot>
        -->
      </el-dialog>


</el-tabs>
  </div>
</template>

<script>

import foot from './middle/foot.vue'
import allUrl from './middle/url.js'

import $ from 'jquery'

export default {
  name: 'reimburse',
  data () {
    return {
      allTotal: 0,
      activeName: 'first',
      //表格数据
      tableData:[],
      tableData2:[],
      user: [],
      provider: [],
      pickArr: [],
      searchAfterTotal: 0,
      orderDetail: [],
      orderAfterDetail: [],
      abc: {},

      total: 0,
      pageSize: 0,
      afterTotal: 0,
      afterPageSize: 0,
      detailTotal: 0,
      detailPageSize: 0,
      detailAfterTotal : 0,
      detailAfterPageSize: 0,

      detaiAfterlUrl: allUrl.buresUrl+ '/getAllApplyWithAuditorUserItems/0?page=',
      detailUrl: allUrl.buresUrl+ '/getAllApplyWithAuditorUserItems/0?page=',
      url: allUrl.buresUrl+ '/getAllApplyWithAuditor?page=',
      afterUrl: allUrl.buresUrl+ '/getAllApplyWithAuditor/1?page=',
      proUrl: allUrl.providerUrl,
      allUrl: allUrl.buresUrl,
      postUrl: allUrl.providerUrl+'getTermExcel',

      dialogTableVisible: false,
      dialogFormVisible: false,
      dialogFormVisible2: false,
      dialogFormVisible3: false,

      pickerOptions2: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      date: '',

        //表单数据
        form: {
          event: '',
          cost: '',
        },
        beforeSearch: {
          user: '',
        },
        afterSearch: {
          user: '',
        },

        formLabelWidth: '120px'
    }
  },
  created(){
    this.loadUserOrder();
    this.loadProvider()
    this.loadUser()
    this.loadUserAfterOrder()
  },
  mounted() {
  },
  methods: {
    postDown() {
      this.$confirm('此操作将下载您的报销单, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          document.getElementById("postSubmit").click()
          this.$message({
            type: 'success',
            message: '下载成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消下载'
          });
        });
    },
    cardClick(tab) {
      let index = tab._data.index
      if(index == '0') {
        this.loadUserOrder()
      }
      else{
        this.loadUserAfterOrder()
      }
    },
    listenPagedetailOrder(data) {
      this.orderAfterDetail = data
    },
    afterSearchBack() {
      this.loadUserAfterOrder()
    },
    loadAfterSearch() {
      this.PostAfterSearch().
      then( result => {
        this.tableData2 = result.message.list
        this.afterTotal = result.message.total
        this.afterPageSize = result.message.pageSize

      })
    },
    listenPagedetailOrder(data) {
      this.orderDetail = data
    },
    PostAfterSearch(){
      let items = this.afterSearch.user
      let allUrl = this.allUrl
      return new Promise(function( resolve, reject) {
        $.ajax({
          type:"POST",
          url: allUrl+'/searchUser/judy/'+items+'/0',
          headers: {
            'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
            'Access-Control-Allow-Origin': '*'
          },
          contentType: "application/json; charset=utf-8",
          dataType: "JSON",
          success(result) {
            resolve(result);

          },
          error(result) {
            reject(result);
          }
        })
      })
    },
    Postsearch(){
      let items = this.beforeSearch.user
      let allUrl = this.allUrl
      return new Promise(function( resolve, reject) {
        $.ajax({
          type:"POST",
          url: allUrl+'/searchUser/'+items+'/0',
          headers: {
            'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
            'Access-Control-Allow-Origin': '*'
          },
          contentType: "application/json; charset=utf-8",
          dataType: "JSON",
          success(result) {
            resolve(result);

          },
          error(result) {
            reject(result);
          }
        })
      })
    },
    searchBack() {
      this.loadUserOrder()
    },
    listenPageAfterOrder(data) {
      this.tableData2 = data
    },
    loadSearch() {
      this.Postsearch().
      then( result => {
        this.tableData = result.message.list
        this.total = result.message.total
        this.pageSize = result.message.pageSize
        this.searchAfterTotal = result.message.list[0].subTotal
      })
    },
    postAllPick() {
      let _this = this;
      let items = this.pickArr
      let allUrl = this.allUrl

      this.$confirm('请问是否确认报销申请?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {


          $.ajax({
            type:"POST",
            url: allUrl+'/judyList',
            data:JSON.stringify(items),
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            //dataType: "JSON",
            success(result) {
              console.log(result);
              _this.loadUserOrder();
            },
            error(result) {
              console.log(result.message)
            }
          });
          this.$message({
            type: 'success',
            message: '审核成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
    },
    handleSelectionChange(val) {
        this.pickArr = val;
        let items = this.pickArr
        let num = 0
        for(let i of items) {
          num = num+i.cost
        }
        this.allTotal = num
    },
    getUserOrder() {
      let allUrl = this.allUrl;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
      			url: allUrl+'/getAllApplyWithAuditor/0',
      			headers: {
      				'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
      			contentType: "application/json; charset=utf-8",
      			dataType: "JSON",
      			success(result) {
      				resolve(result);
      			},
      			error(callback) {
      				reject(callback);
      			}
          })
      })
    },
    getUserAfterOrder() {
      let allUrl = this.allUrl;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
      			url: allUrl+'/getAllApplyWithAuditor/1',
      			headers: {
      				'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
      			contentType: "application/json; charset=utf-8",
      			dataType: "JSON",
      			success(result) {
      				resolve(result);
      			},
      			error(callback) {
      				reject(callback);
      			}
          })
      })
    },
    loadUserAfterOrder() {
      this.getUserAfterOrder()
      .then( result => {
        console.log(result);
        this.tableData2 = result.message.list;
        this.afterTotal = result.message.total;
        this.afterPageSize = result.message.pageSize;
      })
    },
    loadUserOrder() {
      this.getUserOrder()
      .then( result => {
        console.log(result);
        this.tableData = result.message.list;
        this.total = result.message.total;
        this.pageSize = result.message.pageSize;
      })
    },
    //分页拿数据到此页面
    listenPageOrder(data) {
      this.tableData = data;
    },
    //表格函数
    formatter(row, column) {
      return row.address;
    },
    filterTag(value, row) {
      return row.status === value;
    },
    getProvier() {
      let proUrl = this.proUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url: proUrl+'provider/getProviderAndBalance',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {

              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    loadProvider() {
      this.getProvier()
      .then( result => {
        console.log(result);
        this.provider = result.message
      })
    },
    loadUser() {
      this.getUser()
      .then( e => {
        this.user = e
        console.log(e)
      })
    },
    getUser() {
      let proUrl = this.proUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"GET",
            url: proUrl+'/user',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },

    handleEdit(index, row) {
      let _this = this;
      this.dialogFormVisible2 = true
      let items = {
        userId: row.userId
      }


      $.ajax({
            type:"POST",
            url: this.allUrl+'/getAllApplyWithAuditorUserItems/0',
            data: JSON.stringify(items),
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            //dataType: "JSON",
            success(result) {
              console.log(result)
              _this.orderDetail = result.message
            },
            error(result) {
              console.log(result.message)
            }
          });

    },
    afterHandleEdit(index, row) {
      let _this = this;
      this.dialogFormVisible3 = true
      let items = {
        userId: row.userId
      }
      $.ajax({
            type:"POST",
            url: this.allUrl+'/getAllApplyWithAuditorUserItems/1?page=0',
            data: JSON.stringify(items),
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            //dataType: "JSON",
            success(result) {
              console.log(result)
              _this.orderAfterDetail = result.message
            },
            error(result) {
              console.log(result.message)
            }
          });

    },
    //拒绝申请
    handleDelete(index, row) {
      let _this = this;
      let item = row;
      console.log(index)
      this.$confirm('请问是否拒绝此报销审核?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {

          $.ajax({
            type:"POST",
            url: this.allUrl+'/applyAndJuby/2',
            data:JSON.stringify(item),
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            //dataType: "JSON",
            success(result) {
              console.log(result);
              _this.loadUserOrder();
            },
            error(result) {
              console.log(result.message)
            }
          });
          _this.orderDetail.splice(index, 1)
          _this.detailTotal = _this.detailTotal - 1
          this.$message({
            type: 'success',
            message: '拒绝成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
    },
  },
  components: {
    foot
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.reimburseTable .cell{
  text-align: center;
}


</style>



