<template>
  <div class="order_resolve">

    <div class="container-fluid">
      <div class="row detail_title">
        <div class="col-md-2 col-xs-12">订单号</div>
        <div class="col-md-2 col-xs-12">供应商</div>
        <div class="col-md-2 col-xs-12">预订平台订单号</div>
        <div class="col-md-2 col-xs-12">类目</div>
        <div class="col-md-1 col-xs-12">使用日期</div>
        <div class="col-md-1 col-xs-12">金额</div>
        <div class="col-md-1 col-xs-12">付款状态</div>
        <div class="col-md-1 col-xs-12">操作</div>
      </div>
      <div
           v-for = "item of orderItems"
           :class="{row: true,
                    data_body: true,
                         detail_body_hover: current[item]
                    }"
      >
        <div class="col-md-2 col-xs-">{{item.itemNumber}}</div>
        <div class="col-md-2 col-xs-">{{item.name}}</div>
        <div class="col-md-2 col-xs-12">{{item.orderNo}}</div>
        <div class="col-md-2 col-xs-3">{{getItemType(item.type)}}-{{item.category}}</div>
        <div class="col-md-1 col-xs-3">{{item.useDateStr.substring(0, 10)}}</div>
        <div class="col-md-1 col-xs-3">{{item.total}}</div>
        <div class="col-md-1 col-xs-3">{{item.status}}</div>
        <div class="col-md-1 col-xs-3">
          <el-button type="danger" size="mini" @click="cancelApply(item.id)">取消申请</el-button>
        </div>
      </div>
    </div><!--

      <el-table
        :data="orderItems"
        style="width: 100%"
        max-height="250">
        <el-table-column
          fixed
          prop="itemNumber"
          label="订单号"
          width="150">
        </el-table-column>
        <el-table-column
          prop="name"
          label="供应商"
          width="150">
        </el-table-column>
        <el-table-column
          prop="orderNo"
          label="预订平台订单号"
          width="150">
        </el-table-column>
        <el-table-column
          prop="type"
          label="类别"
          width="150">
        </el-table-column>
        <el-table-column
          prop="category"
          label="类目"
          width="150">
        </el-table-column>
        <el-table-column
          prop="useDateStr"
          label="使用日期"
          width="200">
        </el-table-column>
        <el-table-column
          prop="total"
          label="金额"
          width="150">
        </el-table-column>
        <el-table-column
          prop="status"
          label="状态"
          width="150">
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="120">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="deleteRow(scope.$index, tableData4)"
              type="text"
              size="small">
              移除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      -->
    <foot :total="total"
          :pageSize = "pageSize"
          v-on:say = "listenPageOrder"
          :url = "url"
    ></foot>
  </div>
</template>

<script>
import foot from './middle/foot.vue'
import allUrl from './middle/url.js'

export default {
  name: 'order_resolve',
  data () {
    return {
      orderItems: [],
      current: [],
      total: Number,
      pageSize: Number,
      allUrl: allUrl.url,
      url: allUrl.url+'/getUserNonOrder?page=',
    }
  },
  created() {
    this.loadUserOrder()
  },
  methods: {
    cancelApply(_id) {
      console.log(_id)
      let allUrl = this.allUrl
      let url = allUrl+'/updateItemOrder/1/'+_id
      let _this = this

      this.$alert('您确认删除此申请吗', '删除申请', {
          confirmButtonText: '确定',
          callback: action => {
            $.ajax({
              type:"POST",
              url,
              headers: {
                'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
                'Access-Control-Allow-Origin': '*'
              },
              contentType: "application/json; charset=utf-8",
              dataType: "JSON",
              success(result) {
                console.log(result)
                alert(result.message)
                _this.loadUserOrder()
              },
              error(result) {
                console.log(result)
                alert(result.message)
              }
            })

            this.$message({
              type: 'info',
              message: `删除成功！`
            });
          }
        });



    },
    getUserOrder() {
      let allUrl = this.allUrl;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url: allUrl+'/getUserNonOrder',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    listenPageOrder(data) {
      this.orderItems = data;
    },
    loadUserOrder() {
      this.getUserOrder()
      .then( result => {
        console.log(result);
        this.orderItems = result.message.list;
        this.total = result.message.total;
        this.pageSize = result.message.size;
      })
    },
    getItemType(status) {
      if(status == 'hotel') return '酒店';
      if(status == 'flight') return '机票';
      if(status == 'tool') return '旅游用品';
      if(status == 'other') return '其他';
      if(status == 'visa') return '签证';
      if(status == 'car') return '用车';
      if(status == 'flighotel') return '机加酒';
      if(status == 'oneday') return '一日游';
      if(status == 'gongying') return '供应商结算单';
    },
  },
  components: {
    foot
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.detail_title{
  background: rgba(100,100, 100, 0.2);
  font-size: 17px;
}
.detail_body_hover{
  border: 0.1px solid rgba(100, 100, 100, 0.3);
}
.data_body> div{
  overflow:hidden;
  text-overflow : ellipsis;
  white-space : nowrap;
}
</style>



