<template>
  <div class="index">
    <div class="jumbotron">
       <div class="container">
      <h1>Hello </h1>
      <p>欢迎来到支付系统。</p>
    </div>
    </div>

  </div>
</template>

<script>
import foot from './middle/foot.vue'
import allUrl from './middle/url.js'

export default {
  name: 'index',
  data () {
    return {
      id: '',
      allUrl: allUrl.url,

    }
  },
  methods: {
    getUserOrder() {
      let allUrl = this.allUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url: allUrl+'/getUserStatus',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    loadUserOrder() {
      this.getUserOrder()
      .then( result => {
        console.log(result);
        this.id = result.message.role;
      })
    },
  },
  created() {
    this.loadUserOrder();
  },
  components: {
    foot
  }
}
</script>

