<script>

import allUrl from './url.js'
import $ from 'jquery'

export default {
  name: 'orderDetail',
  data () {
    return {
      orderData: [],
      orderData_cost: [],
      current: [],
      allUrl: allUrl.url,
      readyPayOrder: [],
    }
  },
  methods: {
    getDisabeld(row, index) {
      let status = row.status

      if(status == "未付款" || status == "未付款(已拒绝)") {
        return 1
      }
      else{
        return 0
      }
    },
    getPickOrder(rows) {
      this.readyPayOrder = rows
      console.log(this.readyPayOrder)
    },
    getSelectOrder() {
      let order = this.orderData_cost;
      console.log(order);
    },
    postSelectOrder(){
      let order = this.orderData_cost;
      let items = [];
      let _this = this;
      for(let o of order){
        if(o.checked === true){
          delete o.checked;
          items.push(o);
        }
      }

      $.ajax({
        type:"POST",
        url: this.allUrl+'/applyOrderList',
        data: {
          "orderApplies" : JSON.stringify(items)
        },
        //traditional: true,
        dataType: 'JSON',
        headers: {
          'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
          'Access-Control-Allow-Origin': '*'
        },
        //contentType: "application/json; charset=utf-8",
        //dataType: "JSON",
        success(result) {
          console.log(result);
          _this.loadUserOrder();
        },
        error(result) {
          console.log(result.message)
        }
      });

    },
    orderStatus(status){
      if(status == 'NEW') return '未成团';
      if(status == 'END_ORDER') return '订单结束'
      if(status == 'FINISH') return '已成团'
    },
    buttondisabled(status){
      if(status != "未付款") return true
    },

    alertButtonClass(index){
      return `#my${index}`;
    },
    alertButtonName(index) {
      return `my${index}`;
    },
    clickOrder(item) {
      this.current.push(item);
    },
    postAllPayOrderAlert() {
      let _this = this
      this.$confirm('您确定申请付款吗？, 付款申请?', '提示', {
         confirmButtonText: '确定',
         cancelButtonText: '取消',
         type: 'warning'
       }).then(() => {
         _this.postAllPayOrder()
         this.$message({
           type: 'success',
           message: '提交成功！!'
         });
       }).catch(() => {
         this.$message({
           type: 'info',
           message: '已取消申请'
         });
       });
    },
    postAllPayOrder() {
        let data = this.readyPayOrder
        let jsonArr = JSON.stringify(data);
        let _this = this;
      $.ajax({
        type:"POST",
        url: this.allUrl+'/applyOrderList',
        data: {
          "orderApplies" : jsonArr
        },
        //traditional: true,
        dataType: 'JSON',
        headers: {
          'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
          'Access-Control-Allow-Origin': '*'
        },
        //contentType: "application/json; charset=utf-8",
        //dataType: "JSON",
        success(result) {
          console.log(result);
          _this.loadUserOrder();
        },
        error(result) {
          console.log(result.message)
        }
      });
    },
    getItemType(status) {
      if(status == 'hotel') return '酒店';
      if(status == 'flight') return '机票';
      if(status == 'tool') return '旅游用品';
      if(status == 'other') return '其他';
      if(status == 'visa') return '签证';
      if(status == 'car') return '用车';
      if(status == 'flighotel') return '机加酒';
      if(status == 'oneday') return '一日游';
      if(status == 'gongying') return '供应商结算单';
    },
    getOrderData() {
      this.orderData = this.$route.query.id
    },
    getUserOrder() {
      let orderId = this.orderData;
      let allUrl = this.allUrl;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
      			url: allUrl+ '/getOrderItems?itemNumber=' + orderId,
      			headers: {
      				'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
      			contentType: "application/json; charset=utf-8",
      			dataType: "JSON",
      			success(result) {
      				resolve(result);
      			},
      			error(callback) {
      				reject(callback);
      			}
          })
      })
    },
    loadUserOrder() {
      this.getUserOrder()
      .then( result => {
        console.log(result)
        this.orderData_cost = result.message;
      })
    },
  },
  created() {
    this.getOrderData();
    this.loadUserOrder();
    console.log(this.orderData_cost)
  },
  components: {

  }

}
</script>

