<template>
  <div class="order">
    <!--  表格-->
      <!--
      <div class="row data_title">
        <div class="col-md-3 col-xs-3">{{tip.itemNumber}}</div>
        <div class="col-md-3 col-xs-3">{{tip.itemCustomer}}</div>
        <div class="col-md-3 col-xs-3">{{tip.itemFlat}}</div>
        <div class="col-md-3 col-xs-3">{{tip.itemStatus}}</div>
      </div>
      <div class="row data_body"
           v-for = "item of getData"
           @click ="goToOrderDetail(item)"
      >
        <div class="col-md-3 col-xs-3">{{item.itemNumber}}</div>
        <div class="col-md-3 col-xs-3">{{item.customerName}}</div>
        <div class="col-md-3 col-xs-3">{{item.manFlat}}</div>
        <div class="col-md-3 col-xs-3">{{orderStatus(item.status)}}</div>
      </div>

      -->
    <search v-on:listenSearch = "listenSearchOrder"></search>
    <el-table
        class="reimburseTable"
        :data="orderItems"
        border
        style="width: 100%">
            <el-table-column
              prop="itemNumber"
              label="订单号"
              sortable
              width="280">
            </el-table-column>
            <el-table-column
              prop="customerName"
              label="客人姓名"
              width="280">
            </el-table-column>
            <el-table-column
              prop="manFlat"
              label="区域"
              >
            </el-table-column>
            <el-table-column
              prop="status"
              label="状态"
              width="200"
              >
              <template slot-scope="scope">
                <el-tag
                  :type="scope.row.status === 'NEW' ? 'primary' : 'success'  &&  scope.row.status === 'FINISH' ? 'success' : 'wraning' "
                  close-transition>{{orderStatus(scope.row.status)}}</el-tag>
              </template>
            </el-table-column>
            <el-table-column
               fixed="right"
               label="操作"
               width="200">
               <template slot-scope="scope">
                 <el-button
                     type="primary"
                     size="small"
                     @click.native.prevent="goToOrderDetail(scope.$index, orderItems)"
                 >
                     查看
                </el-button>
               </template>
             </el-table-column>
      </el-table>

    <!--  foot -->
    <foot :total="total"
          :pageSize = "pageSize"
          v-on:say = "listenPageOrder"
          :url = "url"
          :form = "form"
    ></foot>
  </div>
</template>

<script>
import foot from './middle/foot.vue'
import $ from 'jquery'
import allUrl from './middle/url.js'
import search from './middle/search.vue'
export default {
  name: 'order',
  data () {
    return {
      tip: {
          itemNumber: '订单号',
          itemCustomer: '联系人',
          itemFlat: '平台',
          itemStatus: '订单状态',
      },
      orderAllData: [],
      number: '',
      customer: '',
      flat: '',
      status: '',
      orderItems: [],
      total: Number,
      pageSize: Number,
      allUrl: allUrl.url,
      url:allUrl.url+'/getUserOrders?page=',
      form: [],
    }
  },

  computed: {
  },
  created(){
    this.loadUserOrder();
  },
  mounted() {

  },
  methods: {
    //修改传入字段name
    orderStatus(status){
      if(status == 'NEW') return '未成团'
      if(status == 'END_ORDER') return '订单结束'
      if(status == 'FINISH') return '已成团'
    },
    //订单分页
    listenPageOrder(data) {
      this.orderItems = data
    },
    //搜索订单分页
    listenSearchOrder(data) {
      console.log(data)
      this.orderItems = data.list
      this.total = data.total
      this.pageSize = data.pageSize
      this.url = this.allUrl +'/selectByParameters/'
      this.form = data.form
      
    },
    //ajax 订单
    getUserOrder() {
      let allUrl = this.allUrl;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
      			url: allUrl+'/getUserOrders',
      			headers: {
      				'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
      			contentType: "application/json; charset=utf-8",
      			dataType: "JSON",
      			success(result) {
      				resolve(result);
      			},
      			error(callback) {
      				reject(callback);
      			}
          })
      })
    },
    //记载ajax 订单
    loadUserOrder() {
      console.log(this.allUrl)
      this.getUserOrder()
      .then( result => {
        this.orderItems = result.message.list;
        this.total = result.message.total;
        this.pageSize = result.message.pageSize;
      })
    },
    //跳转订单详情
    goToOrderDetail(index, rows) {
      let item = rows[index]
      let allUrl = this.allUrl
      let path = allUrl+ '/'+ item.itemNumber
      this.$router.push({
        name: 'order_detail',
        query: {
          id: item.itemNumber
        }
      })
    },
    //获取所有订单
    getUserAllOrder() {
      let allUrl = this.allUrl;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url: `${allUrl}/getUserOrdersWithNoPage`,
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {

              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    loadUserAllOrder() {
      this.getUserAllOrder()
      .then( result => {
        console.log('getUserOrderWithNoPage:');
        console.log(result);
        this.orderAllData = result.message;
      })
    },
  },
  components: {
    foot,
    search
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

.row > div{
  text-align: center;
  border: 0.1px solid rgba(100, 100, 100, 0.1);
}
.row{

  height:50px;
  line-height: 50px;
  margin-top:5px;
}
.data_body{
  cursor: pointer;
}
.data_body:hover{
  border: 0.1px solid rgba(100, 100, 100, 0.3);
}
.data_title{
  font-size: 18px;
}
.reimburseTable .cell{
  text-align: center;
}
</style>



