      this.dialogFormVisible4 = false
      this.postAfterOrderRow = []
    },
    postAfterOrder() {
      let items = this.postAfterOrderRow
      let _this = this
      let allUrl = this.allUrl
      items.payWay = this.getPayNum(this.payFormAfter.payMent)
      items.payNumber = this.payFormAfter.payNumber
      console.log(items)


      this.$confirm('此操作将付款合并订单, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
