              _this.dialogFormVisible4 = false
              _this.payFormAfter.payMent = ''
              _this.payFormAfter.payNumber = ''
              _this.loadUserMonthOrder()
              items = []
              alert(result.message)
            },
            error(callback) {
              console.log(result)
              alert(result.message)
              _this.dialogFormVisible3 = false
              _this.dialogFormVisible4 = false
              items = []
            }
          })
          this.$message({
            type: 'success',
            message: '付款成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消付款'
          });
        });
