    cancelPostPayOrder() {
      this.dialogFormVisible = false
      this.payDetail = []
      this.payForm.payMent = ''
      this.payForm.payNumber = ''
    },
    postPayOrder() {
      let _this = this
      let items = this.payDetail
      let allUrl = this.allUrl
      items.payWay = this.getPayNum(this.payForm.payMent)
      items.payNumber = this.payForm.payNumber
