          console.log(result)
          _this.dialogFormVisible = false
          _this.payDetail = []
          _this.payForm.payMent = ''
          _this.payForm.payNumber = ''
          _this.loadUserOrder()
          alert(result.message)
        },
        error(callback) {
          console.log(result)
          alert(result.message)
        }
      })
    },
    providerTotal(name) {
      for(let i of this.provider){
        if( i.name == name) {

          return i.balance
        }
      }
    },
    getPayNum(str) {
      if(str == "亿佰公账") return 0
      if(str == "亿佰支付宝") return 1
      if(str == "坤刻支付宝") return 2
      if(str == "陈晓璇银行卡") return 3
      if(str == "刘嘉诚银行卡") return 4
      if(str == "刘晓华银行卡") return 5
      if(str == "陈晓璇支付宝") return 6
      if(str == "刘嘉诚支付宝") return 7
      if(str == "刘晓华支付宝") return 8
      if(str == "库存") return 9
    },
    cardClick(tab) {
      let index = tab._data.index
      if(index == '0') {
        console.log('单结申请刷新')
        this.loadUserOrder()
      }
      else if(index == '1'){
        console.log('月结申请刷新')
        this.loadUserMonthOrder()
      }
      else{
        console.log('额度申请刷新')
        this.loadProviderOrder()
      }
