              alert(result.message)
              _this.loadMergeOrderAfter(_this.afterId)
              _this.loadUserMonthOrder()
              console.log(result)
            },
            error(result) {
              alert(result.message)
            }
          });

          this.$message({
            type: 'success',
            message: '删除成功!'
          });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });

