      let _row = row
      this.postAfterOrderRow = row
      this.getMergeOrderAfter(row)
      .then( result => {
        this.allPayAfter = result.message

        //后端没传供应商给我 辣鸡 解决一下
        for(let i of this.allPayAfter) {
          i.name = _row.name
        }

        for(let i of result.message) {
          this.sub_total += i.total
        }
        this.sub_total = `总金额为${this.sub_total}元`
      })
