    },
    postMergeOrder() {
      let items = {}
      items.arr = []
      let proUrl = this.proUrl
      let _this = this

      for(let i of this.allPay) {
        items.arr.push(i.id)
      }

        this.$prompt('请输入名称并合并结算单。', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {
          items.name = value
          console.log(items)
          $.ajax({
            type:"POST",
            url: proUrl+'orderMerge',
            data:JSON.stringify(items),
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              alert(result.message)
              console.log(result)
            },
            error(result) {
              alert(result.message)
            }
          });
          _this.dialogFormVisible2 = false
          _this.allPay = []
          _this.loadUserOrder()
          this.$message({
            type: 'success',
            message: '你的合并订单名称是: ' + value
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
    },
    deleteOnePay(row){
      let index = this.allPay.indexOf(row)
      this.allPay.splice(index, 1)
    },
    showPayDetail(row) {
      this.dialogFormVisible = true
      this.payDetail = row
    },
    getDisabeld(row, index) {
      if(this.allPay.length) {
        let id = row.id
        let index = false
        for(let i of this.allPay) {
          if( i.id === id) index =true
        }
        if(index) return 0
        return 1
      }
      else{
        return 1
      }

    },
    delMergeOrder() {
      this.allPay = []
    },
    getMergeOrder() {
      let allPay = this.allPay
      let select = this.selectOrder

      for(let i of select) {
        allPay.push(i)
      }

      this.$refs.multipleTable.clearSelection()

    },
    getAllPayOrder(row, index) {
      this.selectOrder = row
    },
    cancelApply(row){
      let allUrl = this.allUrl
      let url = allUrl+'/updateItemOrder/2/'+ row.id
      let _this = this

        this.$confirm('您确认拒绝此申请吗, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          $.ajax({
            type:"POST",
            url,
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              console.log(result)
              alert(result.message)
              _this.loadUserOrder()
            },
            error(result) {
              console.log(result)
              alert(result.message)
            }
          })
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
    },
    open2(item) {
        let payWay = this.payWay;
        let payNumber = this.payNumber;

        if(!payWay.length ) {
          alert('付款方式不能为空。')
          return
        }
        if(!payNumber.length){
          alert('付款流水号不能为空。')
          return
        }
        this.$confirm('是否确认已付款？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.postPayMethods(item)
          this.$message({
            type: 'success',
            message: '提交成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });
        });
    },
    postPayMethods(item) {
      let payWay = this.payWay;
      let payNumber = this.payNumber;
      let allUrl = this.allUrl;


      if(payWay == '亿佰公账') payWay = 0;
      if(payWay == '亿佰支付宝') payWay = 1;
      if(payWay == '坤刻支付宝') payWay = 2;
      if(payWay == '陈晓璇银行卡') payWay = 3;
      if(payWay == '刘嘉诚银行卡') payWay = 4;
      if(payWay == '刘晓华银行卡') payWay = 5;
      if(payWay == '陈晓璇支付宝') payWay = 6;
      if(payWay == '刘嘉诚支付宝') payWay = 7;
      if(payWay == '刘晓华支付宝') payWay = 8;
      item.payWay =  payWay;
      item.payNumber = payNumber;
      let _this = this;
      $.ajax({
        type:"POST",
        url: allUrl+'/updateOrderStatus',
        data:JSON.stringify(item),
        headers: {
          'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
          'Access-Control-Allow-Origin': '*'
        },
        contentType: "application/json; charset=utf-8",
        //dataType: "JSON",
        success(result) {
            _this.loadUserOrder();
            _this.payWay = '';
            _this.payNumber = '';
          console.log(result);
        },
        error(result) {
          console.log(result.message)
          _this.payWay = '';
          _this.payNumber = '';
        }
      });



    },
    listenPageOrder(data) {
      this.orderItems = data
    },
    listenPageMonthOrder(data) {
      this.orderMonthItems = data
    },
    listenPagePorOrder(data) {
      this.providerData = data
    },
    getId(index){
      return `my${index}`;
    },
    getToggle(index){
      return `#my${index}`
    },
    getProvierOrder() {
      let proUrl = this.proUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"GET",
            url: proUrl+'resource/getAllWithNoJudy/0/0',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {

              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    getProvier() {
      let proUrl = this.proUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url: proUrl+'provider/getProviderAndBalance',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {

              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    getUserOrder() {
      let allUrl = this.allUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url: allUrl+'/getFinanceNonOrder',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {

              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    getUserMonthOrder() {
      let proUrl = this.proUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url: proUrl+'orderMerge/getAll/0/0',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {

              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    loadProvider() {
      this.getProvier()
      .then( result => {
        console.log(result);
        this.provider = result.message
      })
    },
    loadUserMonthOrder() {
      let proUrl = this.proUrl
      this.getUserMonthOrder()
      .then( result => {
        console.log(result);
        this.orderMonthItems = result.message.list;
        this.monthTotal = result.message.total;
        this.monthPageSize = result.message.size;
        this.monthUrl = proUrl+ 'orderMerge/getAll/0/'
      })
    },
    loadProviderOrder() {
      let proUrl = this.proUrl
      this.getProvierOrder()
      .then( result => {
        console.log(result);
        this.providerData= result.message.list;
        this.proTotal = result.message.total;
        this.proPageSize = result.message.size;
        this.providerUrl = proUrl + 'resource/getAllWithNoJudy/0/'
      })
    },
    loadUserOrder() {
      let allUrl = this.allUrl
      this.getUserOrder()
      .then( result => {
        console.log(result);
        this.orderItems = result.message.list;
        this.total = result.message.total;
        this.pageSize = result.message.size;
        this.url = allUrl+ '/getFinanceNonOrder?page='
      })
    },
    getDate(result){
      let date = new Date(result);
      let first = String(date).substring(1, 10);
      let last = String(date).substring(11, 19);
      let newDate = first+' '+last;
      return date;
    },
    getItemType(status) {
      if(status == 'hotel') return '酒店';
      if(status == 'flight') return '机票';
      if(status == 'tool') return '旅游用品';
      if(status == 'other') return '其他';
      if(status == 'visa') return '签证';
      if(status == 'car') return '用车';
      if(status == 'flighotel') return '机加酒';
      if(status == 'oneday') return '一日游';
      if(status == 'gongying') return '供应商结算单';
    },
  },
  components: {
    foot
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

.row > div{
  text-align: center;
  border: 0.1px solid rgba(100, 100, 100, 0.1);
}
.row{

  height:50px;
  line-height: 50px;
  margin-top:5px;
}
.payClass{
  height:35px;

  position: relative;
  top:10px;

}
.data_body{
  cursor: pointer;
}
.data_body:hover{
  border: 0.1px solid rgba(100, 100, 100, 0.3);
}
.data_title{
  font-size: 18px;
}
.detail_title{
  background: rgba(100,100, 100, 0.2);
  font-size: 17px;
}
.detail_body_hover{
  border: 0.1px solid rgba(100, 100, 100, 0.3);
}
.orderDate{
  font-size: 12px;
}
.data_body> div{
  overflow:hidden;
  text-overflow : ellipsis;
  white-space : nowrap;
}
.Zcol{
  border: 1px solid rgba(1,1,1,0.2);
  text-align: center;
  font-size: 16px;
  height:40px;
  line-height: 40px;
  border-radius: 3px;
  margin-top:1px;
}
.cell{
  text-align: center;
}
</style>



