class Kok {
  constructor(name = 'kok') {
    this.name = name
  }
  //嘿嘿嘿
  callMyName() {
    console.log(`my name is ${this.name}！`)
  }

  //数组去重
  removal(arr) {
    return [...new Set([...arr])]
  }

  /*
  babel工作原理

  1.解析:将代码字符串解析成抽象语法树
  2.变换:操作抽象语法树，进行改变
  3.根据变换后的抽象语法树再生成代码字符串
  */

}

let kok = new Kok()

module.exports = kok



