<template>
  <div class="reimburse">

    <form method="post" :action="postUrl"  v-show="false">
      <input type="text"  name="startDate" :value="date[0]"></input>
      <input type="text" name="endDate" :value="date[1]"></input>
      <input type="submit" id="postSubmit" style="margin-top:10px;"></input>
    </form>
    <!--  按钮页面 -->
    <el-row :gutter="20">
      <el-col :span="20">
        <div style="margin-left:50px;">
            <div class="block" style="margin-top:10px;margin-bottom:10px;">
              <el-date-picker
                v-model="date"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="pickerOptions2">
              </el-date-picker>
            </div>
            <el-button type="primary" @click="dialogFormVisible = true" style="margin-top:10px;">新增</el-button>
            <el-button type="primary" @click="downReimburse" style="margin-top:10px;">导出</el-button>


        </div>

      </el-col>
    </el-row>

    <!--  表格页面-->
    <el-table
    class="reimburseTable"
    :data="tableData"
    style="width: 100%"
    >
        <el-table-column
          prop="id"
          label="事由ID"
          sortable
          width="100">
        </el-table-column>
        <el-table-column
          prop="applyTimeStr"
          label="申请时间"
          sortable
          width="200">
        </el-table-column>
        <el-table-column
          prop="event"
          label="申请事由"
          sortable
          width="300">
        </el-table-column>
        <el-table-column
          prop="cost"
          label="申请金额"
          sortable
          width="300"
          >
        </el-table-column>
        <el-table-column
          prop="category"
          label="类别"
          width="100"
          >
        </el-table-column>
        <el-table-column
          prop="status"
          label="申请金额"
          width="280"
          :filters="[{ text: '已拒绝', value: '已拒绝' }, { text: '未审核', value: '未审核' }, {text: '已审核', value: '已审核'}]"
          :filter-method="filterTag"
          filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.status === '未审核' ? 'primary' : 'success' && scope.row.status === '已拒绝' ? 'danger' : 'warning' "
              close-transition>{{scope.row.status}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="nickName"
          label="审核人"
          sortable
          width="100">
        </el-table-column>
        <el-table-column
          prop="auditorDateStr"
          label="审核时间"
          sortable
          width="120">
        </el-table-column>
      </el-table>


      <!--  新增表单页面 -->
      <el-dialog title="申请报销" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="活动区域" :label-width="formLabelWidth">
            <el-select v-model="form.category" placeholder="请选择类目">
              <el-option label="代订住宿费" value="代订住宿费"></el-option>
              <el-option label="日用品" value="日用品"></el-option>
              <el-option label="定额" value="定额"></el-option>
              <el-option label="交通费" value="交通费"></el-option>
              <el-option label="快递费" value="快递费"></el-option>
              <el-option label="汽油" value="汽油"></el-option>
              <el-option label="餐饮" value="餐饮"></el-option>
              <el-option label="其他" value="其他"></el-option>
              <el-option label="杂费支出" value="杂费支出"></el-option>
              <el-option label="预付费销售" value="预付费销售"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="其他类目" :label-width="formLabelWidth" v-show="form.category == '其他'">
            <el-input v-model="form.other" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="申请报销事由" :label-width="formLabelWidth">
            <el-input v-model="form.event" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="申请金额" :label-width="formLabelWidth">
            <el-input v-model="form.cost" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">关 闭</el-button>
          <el-button type="primary" @click="postNewBurese">申 请</el-button>
        </div>
      </el-dialog>


    <!--  foot -->
    <foot :total="total"
          :pageSize = "pageSize"
          v-on:say = "listenPageOrder"
          :url = "url"
    ></foot>
  </div>
</template>

<script>

import foot from './middle/foot.vue'
import allUrl from './middle/url.js'

import $ from 'jquery'

export default {
  name: 'reimburse',
  data () {
    return {
      allUrl: allUrl.buresUrl,
      //表格数据
      tableData:[],

      total: 0,
      pageSize: 0,
      url: allUrl.buresUrl+'/getAllApplyByUser?page=',
      proUrl: allUrl.providerUrl,
      postUrl: allUrl.providerUrl+'createExcel',

      dialogTableVisible: false,
      dialogFormVisible: false,

        //表单数据
        form: {
          event: '',
          cost: '',
          category: '',
          other: '',
        },
        formLabelWidth: '120px',
        pickerOptions2: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        date: ''

    }
  },

  computed: {
  },
  created(){
    this.loadUserOrder();
  },
  mounted() {
  },
  methods: {
    downReimburse(){
      let _this = this
      let proUrl = this.proUrl
      let _date = this.date
      let date = {
        startDate: _date[0],
        endDate: _date[1]
      }
      this.$confirm('此操作将下载所有员工的报销单, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          document.getElementById("postSubmit").click()
          this.$message({
            type: 'success',
            message: '下载成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消下载!'
          });
        });
    },
    getStatusClass(stauts) {
      let sta = status;
      if(sta === '未审核') return 'primary';
      if(sta === '已拒绝') return 'danger';
      if(sta === '已审核' ) return 'success';
      if(sta == '审核中') return 'warning';
    },
    getUserOrder() {
      let allUrl = this.allUrl;
      let _this = this;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
      			url: allUrl+'/getAllApplyByUser',
      			headers: {
      				'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
      			contentType: "application/json; charset=utf-8",
      			dataType: "JSON",
      			success(result) {

      				resolve(result);
      			},
      			error(callback) {
      				reject(callback);
      			}
          })
      })
    },
    loadUserOrder() {
      this.getUserOrder()
      .then( result => {
        console.log(result);
        this.tableData = result.message.list;
        this.total = result.message.total;
        this.pageSize = result.message.pageSize;
      })
    },
    listenPageOrder(data) {
      this.tableData = data;
    },
    formatter(row, column) {
      return row.address;
    },
    filterTag(value, row) {
      return row.status === value;
    },
    handleEdit(index, row) {
      console.log(index, row);
    },
    handleDelete(index, row) {
      console.log(index, row);
    },
    postNewBurese(){
      let items = this.form;
      let allUrl = this.allUrl;
      let _this = this;
      if(items.other == '') {
        delete items.other

      }
      else{
        items.category = items.other
        delete items.other
      }

      console.log(items);

      if(isNaN(items.cost)) {
        alert('金额必须为数字');
        return false;
      }

      $.ajax({
        type:"POST",
        url: allUrl+'/addOneEvent',
        headers: {
          'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
          'Access-Control-Allow-Origin': '*'
        },
        data: JSON.stringify(items),
        contentType: "application/json; charset=utf-8",
        dataType: "JSON",
        success(result) {
          _this.loadUserOrder();
          items.event = ''
          items.cost = ''
          items.category = ''
          items.other = ''
          console.log(result)
        },
        error(callback) {
          console.log(callback)
        }
      })

    }
  },
  components: {
    foot
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.reimburseTable .cell{
  text-align: center;
}


</style>



