<template>
  <div class="provider">
    <!-- 供应商按钮 -->

    <el-card class="box-card">
      <div slot="header" class="clearfix" >
        <el-button type="primary" class="button_wrap" @click="dialogFormVisible = true">新增供应商</el-button>
        <el-button type="success" class="button_wrap" @click="dialogFormVisible3 = true">库存申请</el-button>
      </div>
    </el-card>
    <!-- 供应商表格-->
    <el-table
        :data="providerData"
        tooltip-effect="dark"
        border
        style="width: 100%">
        <el-table-column
          prop="id"
          label="id"
          width="100">
        </el-table-column>
        <el-table-column
          prop="name"
          label="供应商名称"
          width="200">
        </el-table-column>
        <el-table-column
          prop="memo"
          label="详情"
          width="430"
          >
        </el-table-column>
        <el-table-column
          prop="category"
          label="类别"
          width="200"
          >

        </el-table-column>
        <el-table-column
          prop="balance"
          label="余额/库存"
          width="100">
        </el-table-column>
        <el-table-column
          prop="cost"
          label="成本金额"
          width="100">
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="230">
          <template slot-scope="scope">
            <el-button @click="deleteProvider(scope.row)" type="danger" size="small">删除</el-button>
            <el-button @click="infoProviderCost(scope.row)" type="warning" size="small">修改库存</el-button>
          </template>
        </el-table-column>
    </el-table>
    <!-- 新增供应商alert -->
    <el-dialog title="新增供应商" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="供应商名称" label-width="120px">
            <el-input v-model="form.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="供应商详情" label-width="120px">
            <el-input
              type="textarea"
              :rows="2"
              placeholder="请输入内容"
              v-model="form.memo">
            </el-input>
          </el-form-item>
          <el-form-item label="类别" label-width="120px">
            <el-select v-model="form.category" placeholder="类别" >
              <el-option label="余额类" value="余额类"></el-option>
              <el-option label="库存类" value="库存类"></el-option>
              <el-option label="单结类" value="单结类"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="newProvider">确 定</el-button>
        </div>
      </el-dialog>

    <!-- 修改供应商alert -->
    <el-dialog title="修改供应商成本金额" :visible.sync="dialogFormVisible2">
        <el-form :model="infoCostRow">
          <el-form-item label="供应商名称" label-width="120px">
            <el-input v-model="infoCostRow.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="供应商详情" label-width="120px">
            <el-input
              type="textarea"
              :rows="2"
              placeholder="请输入内容"
              v-model="infoCostRow.memo">
            </el-input>
          </el-form-item>

          <el-form-item label="类别" label-width="120px" >
            <el-select v-model="infoCostRow.category" placeholder="类别"  >
              <el-option label="余额类" value="余额类"></el-option>
              <el-option label="库存类" value="库存类"></el-option>

            </el-select>
          </el-form-item>

          <el-form-item label="增减余额/库存" label-width="120px">
            <el-radio-group v-model="sumExist">
              <el-radio-button label="增加余额/库存"></el-radio-button>
              <el-radio-button label="减少余额/库存"></el-radio-button>
            </el-radio-group>
          </el-form-item>

          <el-form-item label="金额/库存" label-width="120px">
            <el-input v-model="newNum" auto-complete="off"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancelAlert">取 消</el-button>
          <el-button type="primary" @click="infoProviderAlert">确 定</el-button>
        </div>
      </el-dialog>

    <!-- 新增库存申请alert -->
    <el-dialog title="库存申请" :visible.sync="dialogFormVisible3" >
      <el-form :model="providerForm">

        <el-form-item label="供应商" label-width="80px">
          <el-select v-model="providerForm.name" filterable placeholder="请选择">
            <el-option
              v-for="item in providerData"
              :key="item.id"
              :label="item.name"
              :value="item.name">
            </el-option>
          </el-select>
        </el-form-item>



        <el-form-item label="成本" label-width="80px">
          <el-input v-model="providerForm.total" style="width: 29%;"></el-input>
        </el-form-item>
        <el-form-item label="额度/库存" label-width="80px">
          <el-input v-model="providerForm.balance" style="width: 29%;"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible3 = false">取 消</el-button>
        <el-button type="primary" @click="applyProvider">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import url from './middle/url.js'
export default {
  name: 'provider',
  data () {
    return {
      providerUrl: url.providerUrl,
      providerData: [],
      dialogFormVisible: false,
      dialogFormVisible2: false,
      dialogFormVisible3: false,
      infoCostRow: {},
      sumExist: null,
      newNum: null,
      form: {
        name: '',
        memo: '',
      },
      providerForm: {
        total: '',
        balance: '',
        name: '',
      }
    }
  },
  methods: {
    applyProviderAjax(items) {
        let providerUrl = this.providerUrl;
        return new Promise( (resolve, reject) =>{
            $.ajax({
              type:"POST",
              url: providerUrl+'resource',
              data: JSON.stringify(items),
              headers: {
                'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
                'Access-Control-Allow-Origin': '*'
              },
              contentType: "application/json; charset=utf-8",
              dataType: "JSON",
              success(result) {
                resolve(result);
              },
              error(callback) {
                reject(callback);
              }
            })
        })
    },
    //申请
    applyProvider() {
      let items = this.providerForm
      let _this = this

      this.$confirm('此操作将申请库存额度, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          _this.applyProviderAjax(items).
          then( e => {
            console.log(e)
            this.providerForm.total = ''
            this.providerForm.balance = ''
            this.providerForm.name = ''
            this.dialogFormVisible3 = false
          })
          this.$message({
            type: 'success',
            message: '申请成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消申请'
          });
        });
    },
    //衔接修改供应商alert
    infoProviderCost(row) {
      this.dialogFormVisible2 = true
      this.infoCostRow = row
    },
    //修改供应商alert
    infoProviderAlert() {
      let _this = this
      this.$confirm('确认修改余额吗？ ', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          _this.infoProviderDone()
          _this.dialogFormVisible2 = false
          this.$message({
            type: 'success',
            message: '修改成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消修改'
          });
        });
    },
    cancelAlert() {
      this.dialogFormVisible2 = false
      this.loadProvider()
    },
    //修改供应商ajax
    infoProviderDone() {
      let index = null
      let _this = this
      let providerUrl = this.providerUrl
      let balance = this.infoCostRow.balance
      if(this.sumExist == '减少余额/库存') {
         balance = Number(balance) - Number(this.newNum)
      }
      if(this.sumExist == '增加余额/库存') {
        balance = Number(balance) + Number(this.newNum)
      }
      this.infoCostRow.category = this.infoCostRow.category
      this.infoCostRow.balance = balance
      let infoCostRow = this.infoCostRow
      console.log(infoCostRow)
      $.ajax({
        type:"POST",
        url: providerUrl+'provider',
        data: JSON.stringify(infoCostRow),
        headers: {
          'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
          'Access-Control-Allow-Origin': '*'
        },
        contentType: "application/json; charset=utf-8",
        dataType: "JSON",
        success(result) {
          _this.loadProvider()
          console.log(result)
        },
        error(result) {
          console.log(result)
        }
      })

    },
    //删除供应商ajax
    deleteProvider(row) {
      let id = row.id
      let providerUrl = this.providerUrl
      let _this = this
        this.$confirm('确认删除供应商吗?', '提示', {
         confirmButtonText: '确定',
         cancelButtonText: '取消',
         type: 'warning'
       }).then(() => {
         $.ajax({
           type:"POST",
           url: providerUrl+'provider/'+id,
           headers: {
             'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
             'Access-Control-Allow-Origin': '*'
           },
           contentType: "application/json; charset=utf-8",
           dataType: "JSON",
           success(result) {
             _this.loadProvider()
             console.log(result)
           },
           error(callback) {
             alert(callback)
           }
         })
         this.$message({
           type: 'success',
           message: '删除供应商成功！!'
         });
       }).catch(() => {
         this.$message({
           type: 'info',
           message: '已取消删除'
         });
       });

    },
    //新增供应商ajax
    newProvider() {
      let form = this.form
      let providerUrl = this.providerUrl
      let _this = this
      console.log(form)
      form.balance = 0
      form.cost = 0
      this.$alert('确认新增供应商吗？', '新增供应商', {
          confirmButtonText: '确定',
          callback: action => {
            this.dialogFormVisible = false

            $.ajax({
              type:"POST",
              url: providerUrl+'provider',
              data: JSON.stringify(form),
              headers: {
                'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
                'Access-Control-Allow-Origin': '*'
              },
              contentType: "application/json; charset=utf-8",
              dataType: "JSON",
              success(result) {
                _this.loadProvider()
                form.name=''
                form.memo=''
                form.category=''
                console.log(result)
              },
              error(callback) {
                if(callback.code === 0) {
                  alert('新增失败,请选择类别！')
                }
              }
            })
            this.$message({
              type: 'info',
              message: `新增供应商成功！`
            });

          }
        });
    },
    //获取ajax
    getProvier() {
      let providerUrl = this.providerUrl;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"GET",
            url: providerUrl+'provider',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    getCateGory(c) {
      if(c === 0) return '余额类'
      if(c === 1) return '库存类'
    },
    //加载ajax
    loadProvider() {
      this.getProvier()
      .then( result => {
        console.log(result);
        this.providerData = result.value
      })
    },
  },
  created() {
    this.loadProvider()
  },
  components: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.row > div{
  text-align: center;
  border: 0.1px solid rgba(100, 100, 100, 0.1);
}
.row{
  height:50px;
  line-height: 50px;
  margin-top:5px;
}
.data_body{
  cursor: pointer;
}
.data_body:hover{
  border: 0.1px solid rgba(100, 100, 100, 0.3);
}
.data_title{
  font-size: 18px;
}
.button_wrap{
  margin: 20px 0;
}

.grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
.cell{
  text-align: center;
}
</style>



