<template>
  <el-row :gutter="20" class="search">
    <el-col :span="1">
      <div class="grid-content bg-purple">
      </div>
    </el-col>
    <el-col :span="22">
      <div class="grid-content bg-purple">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">

          <!-- 订单号-->
          <el-form-item label="订单号">
            <el-input v-model="formInline.itemNumber" placeholder=""></el-input>
          </el-form-item>

          <!-- 客人姓名-->
          <el-form-item label="客人姓名">
            <el-input v-model="formInline.customerName" placeholder=""></el-input>
          </el-form-item>

          <!-- 区域-->
          <el-form-item label="平台">
            <el-select v-model="formInline.manFlat" placeholder="">
              <el-option label="携程" value="携程"></el-option>
              <el-option label="蚂蜂窝" value="蚂蜂窝"></el-option>
              <el-option label="自营" value="自营"></el-option>
              <el-option label="代理" value="代理"></el-option>
            </el-select>
          </el-form-item>

          <!-- 状态-->
          <el-form-item label="状态">
            <el-select v-model="formInline.status" placeholder="">
              <el-option label="未成团" value="NEW"></el-option>
              <el-option label="已成团" value="FINISH"></el-option>
              <el-option label="结束订单" value="END_ORDER"></el-option>
              <el-option label="问题订单" value="PROBLEM"></el-option>
              <el-option label="取消订单" value="CANCEL"></el-option>
            </el-select>
          </el-form-item>

          <!-- 搜索 -->
          <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-col>
    <el-col :span="1">
      <div class="grid-content bg-purple">
      </div>
    </el-col>
  </el-row>
</template>

<script>
import allUrl from './url.js'
export default {
  name: 'search',
  data () {
    return {
      formInline: {
          itemNumber: '',
          customerName :'',
          manFlat: '',
          status: ''
      },
      orderAllData: [],
      allUrl: allUrl.url,
    }
  },
  methods: {
    onSubmit() {
      let form = this.formInline
      this.getSearchOrder(form)
      .then( result => {
        let data = result.message
        data.form = form
        console.log('search')
        console.log(data.form)
        this.pageSearchSay(data)
      })
    },
    pageSearchSay(data) {
      this.$emit('listenSearch', data)
    },
    getSearchOrder(form) {
      let allUrl = this.allUrl

      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            data: JSON.stringify(form),
      			url: allUrl+'/selectByParameters/1',
      			headers: {
      				'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
      			contentType: "application/json; charset=utf-8",
      			dataType: "JSON",
      			success(result) {
      				resolve(result);
      			},
      			error(callback) {
      				reject(callback);
      			}
          })
      })
    },

  },
  created() {
    this.loadUserAllOrder()
  },
  components: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
.search{
  margin-top:20px;
}
</style>



