<template>
  <div class="settle_item_order">
    <!-- file donwload-->
    <form method="post" :action="downSettleOrderUrl"  v-show="false">
      <input type="submit" id="postSubmit" style="margin-top:10px;"></input>
    </form>

    <el-row :gutter="20">
      <el-col :span="20">
        <div style="margin-left:50px;">
            <div class="block" style="margin-top:10px;margin-bottom:10px;">
              <el-date-picker
                v-model="date"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                value-format="yyyy-MM-dd"
                :picker-options="pickerOptions2">
              </el-date-picker>
            </div>
            <el-button type="primary" @click="searchSettleOrder" style="margin-top:10px;">搜索</el-button>
            <el-button type="danger" @click="emptySettleOrder = true" style="margin-top:10px;">清空</el-button>
            <el-button type="default" @click="downSettleOrder" style="margin-top:10px;" v-show="isSearch" >导出</el-button>


        </div>

      </el-col>
    </el-row>

    <el-table
        :data="data"
        stripe
        v-loading="loading"
        style="width: 100%">
        <el-table-column
          prop="itemNumber"
          label="订单号"
          width="280">
        </el-table-column>
        <el-table-column
          prop="user"
          label="账号"
          width="280">
        </el-table-column>
        <el-table-column
          prop="startDate"
          label="接单日期"
          width="280">
        </el-table-column>
        <el-table-column
          prop="manFlat"
          label="平台"
          width="180">
        </el-table-column>
        <el-table-column
          prop="reason"
          label="原因"
          width="380"
          >
        </el-table-column>
      </el-table>

      <foot :total="total"
            :pageSize = "pageSize"
            v-on:say = "listenPageOrder"
            :url = "pageUrl"
            :search="search"
      ></foot>
  </div>
</template>

<script>
import axios from 'axios'
import url from './middle/url.js'
import foot from './middle/foot2.vue'

export default {
  data () {
    return {
      proUrl: url.providerUrl,
      downSettleOrderUrl: url.providerUrl+ 'getItemNumberException',

      data: [],
      total: 0,
      pageSize: 0,
      pageUrl: url.providerUrl+ 'queryItemNumberSettleOrder/',


      searchData: [],
      searchTotal: 0,
      searchPageSize: 0,
      searchPageSize: url.providerUrl+ 'queryItemNumberSettleOrder/',

      isSearch: false,
      search: [],
      date: '',
      loading: false,
    }
  },
  created(){
    this.getSettleOrder()
  },
  mounted() {
  },
  methods: {
    searchSettleOrder() {

      let url = this.proUrl
      let _date = this.date
      let _this = this
      this.loading = true

      console.log(_date)
      axios.get(`${url}queryItemNumberSettleOrder/1`, {
        params: {
          startDate: _date[0],
          endDate: _date[1]
        }
      })
      .then(e => {
        console.log(e)
        _this.data = e.data.message.dataList
        _this.total = e.data.message.num
        _this.pageSize = e.data.message.pageSize
        _this.search = _date
        _this.isSearch = true
        _this.pageUrl = _this.searchPageSize
        _this.loading = false
      })
    },
    emptySettleOrder() {
      this.getSettleOrder()
    },
    getSettleOrder() {
      let _this = this
      axios.get(this.proUrl + 'queryItemNumberSettleOrder/1')
      .then(e => {
        console.log(e)
        _this.data = e.data.message.dataList
        _this.total =e.data.message.num
        _this.pageSize = e.data.message.pageSize
      })
    },
    listenPageOrder(data) {
      console.log(data)
      this.data = data
    },
    pickerOptions2: {
      shortcuts: [{
        text: '最近一周',
        onClick(picker) {
          const end = new Date();
          const start = new Date();
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
          picker.$emit('pick', [start, end]);
        }
      }, {
        text: '最近一个月',
        onClick(picker) {
          const end = new Date();
          const start = new Date();
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
          picker.$emit('pick', [start, end]);
        }
      }, {
        text: '最近三个月',
        onClick(picker) {
          const end = new Date();
          const start = new Date();
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
          picker.$emit('pick', [start, end]);
        }
      }]
    },
    downSettleOrder(){
      this.$confirm('此操作将下载错误订单, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          document.getElementById("postSubmit").click()
          this.$message({
            type: 'success',
            message: '下载成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消下载!'
          });
        });
    },
  },
  components: {
    foot
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.cell{
  text-align: center;
}


</style>



