import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import Order from '@/components/order'
import Order_resolve from '@/components/order_resolve'
import Order_reject from '@/components/order_reject'
import Pay from '@/components/pay'
import Unpay from '@/components/unpay'
import Order_detail from '@/components/middle/order_detail'
import Reimburse from '@/components/reimburse'
import Reimbursed from '@/components/reimbursed'
import Provider from '@/components/provider'
import SettleOrder from '@/components/settle_order'
import settle_item_order from '@/components/settle_item_order'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/order',
      name: 'order',
      component: Order
    },
    {
      path: '/order/:id',
      name: 'order_detail',
      component: Order_detail
    },
    {
      path: '/order_resolve',
      name: 'order_resolve',
      component: Order_resolve
    },
    {
      path: '/unpay',
      name: 'unpay',
      component: Unpay
    },
    {
      path: '/order_reject',
      name: 'order_reject',
      component: Order_reject
    },
    {
      path: '/pay',
      name: 'pay',
      component: Pay
    },
    {
      path: '/reimburse',
      name: 'reimburse',
      component: Reimburse
    },
    {
      path: '/reimbursed',
      name: 'reimbursed',
      component: Reimbursed
    },
    {
      path: '/provider',
      name: 'provider',
      component: Provider
    },
    {
      path: '/settle_order',
      name: 'settle_order',
      component: SettleOrder
    },
    {
      path: '/settle_item_order',
      name: 'settle_item_order',
      component: settle_item_order
    }
  ]
})



