<template>
  <div class="settle_order">
    <!-- file donwload-->
    <form method="post" :action="downSettleOrderUrl"  v-show="false">
      <input type="submit" id="postSubmit" style="margin-top:10px;"></input>
    </form>
    <el-table
        :data="data"
        stripe
        style="width: 100%">
        <el-table-column
          prop="itemNumber"
          label="订单号"
          width="180">
        </el-table-column>
        <el-table-column
          prop="manFlat"
          label="平台"
          width="180">
        </el-table-column>
        <el-table-column
          prop="nickname"
          label="定制师"
          width="180">
        </el-table-column>
        <el-table-column
          prop="subtotal"
          label="抓取总金额">
        </el-table-column>
        <el-table-column
          prop="subtotalGUI"
          label="直连总金额">
        </el-table-column>
        <el-table-column
          prop="reason"
          label="原因">
        </el-table-column>
      </el-table>

      <foot :total="total"
            :pageSize = "pageSize"
            v-on:say = "listenPageOrder"
            :url = "pageUrl"
      ></foot>
  </div>
</template>

<script>
import axios from 'axios'
import url from './middle/url.js'
import foot from './middle/foot2.vue'

export default {
  data () {
    return {
      proUrl: url.providerUrl,
      downSettleOrderUrl: url.providerUrl+ 'getExceptionOrder',

      data: [],
      total: 0,
      pageSize: 0,
      pageUrl: url.providerUrl+ 'querySettleOrder/'
    }
  },
  created(){
    this.getSettleOrder()
  },
  mounted() {
  },
  methods: {
    getSettleOrder() {
      let _this = this
      axios.get(this.proUrl + 'querySettleOrder/1')
      .then(e => {
        console.log(e)
        _this.data = e.data.message.dataList
        _this.total =e.data.message.num
        _this.pageSize = e.data.message.pageSize
      })
    },
    listenPageOrder(data) {
      this.data = data
    },
    downSettleOrder(){
      this.$confirm('此操作将下载错误订单, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          document.getElementById("postSubmit").click()
          this.$message({
            type: 'success',
            message: '下载成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消下载!'
          });
        });
    },
  },
  components: {
    foot
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.cell{
  text-align: center;
}


</style>



