  <div id="app">
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
      <el-menu-item index="1" >
        <a href="http://112.124.107.209:8989/#/admin/order">直连</a>
      </el-menu-item>

      <el-submenu index="2" v-show=" id == 'admin' || id=='inspector'">
        <template slot="title">财务管理</template>
        <el-menu-item index="1-1">
            <router-link to="/pay">财务未审核</router-link>
        </el-menu-item>
        <el-menu-item index="1-2">
            <router-link to="/unpay">财务已审核</router-link>
        </el-menu-item>
      </el-submenu>

      <el-menu-item index="3" v-show=" id == 'admin' || id=='inspector'">
        <router-link to="/reimbursed">报销审核</router-link>
      </el-menu-item>

      <el-menu-item index="3" v-show=" id == 'admin' || id == 'resource' || id=='inspector' ">
        <router-link to="/provider">供应商</router-link>
      </el-menu-item >

      <el-menu-item index="4"  v-show=" id == 'admin' || id == 'CUSTO'  || id == 'manager' || id=='inspector' || id=='user'">
        <router-link to="/order">我的订单</router-link>
      </el-menu-item>

      <el-menu-item index="5" v-show=" id == 'admin' || id == 'CUSTO'  || id == 'manager' || id=='inspector' || id=='user'">
