      </el-menu-item>

      <el-menu-item index="6" v-show=" id == 'admin' || id == 'CUSTO'  || id == 'manager' || id=='inspector' || id=='user'">
        <router-link to="/order_reject">已付款</router-link>
      </el-menu-item>

      <el-menu-item index="7" v-show=" id == 'admin' || id == 'CUSTO'  || id == 'manager' || id == 'resource' || id=='inspector' || id=='user'">
        <router-link to="/reimburse">报销申请</router-link>
      </el-menu-item>

      <el-submenu index="9" v-show=" id == 'admin'|| id=='inspector'">
        <template slot="title">查询异常订单</template>
        <el-menu-item index="10-1">
            <router-link to="/settle_order">异常金额查询</router-link>
        </el-menu-item>
        <el-menu-item index="10-2">
            <router-link to="/settle_item_order">未上传数据查询</router-link>
        </el-menu-item>
      </el-submenu>

    </el-menu>


