    <router-view/>
  </div>
</template>

<script>
import allUrl from './components/middle/url.js'
export default {
  name: 'app',
  data () {
    return {
      id: '',
      msg: 'qinker',
      orderNumber: 1,
      allUrl: allUrl.url,
      routerUrl: allUrl.routerUrl,
      activeIndex: '1',
    }
  },
  created() {
    this.loadUserOrderNumber()
    this.loadUserOrder()
  },
  mounted() {
  },
  methods: {

    handleSelect(key, keyPath) {
        console.log(key, keyPath);
    },
    getUserOrderNumber() {
      let allUrl = this.allUrl;
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
      			url: allUrl+'/getNonAuditingOrderNumberForFinance',
      			headers: {
      				'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
      			contentType: "application/json; charset=utf-8",
      			dataType: "JSON",
      			success(result) {
      				resolve(result);
      			},
      			error(callback) {
      				reject(callback);
      			}
          })
      })
    },
    loadUserOrderNumber() {
      let _this = this;
      _this.getUserOrderNumber()
      .then( result => {
        _this.orderNumber = result.message;

      })

      setInterval(function() {
        _this.getUserOrderNumber()
        .then( result => {
          _this.orderNumber = result.message;

        })
      }, 5000)

    },
    getUserOrder() {
      let allUrl = this.allUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url: allUrl+'/getUserStatus',
            headers: {
              'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    loadUserOrder() {
      this.getUserOrder()
      .then( result => {
        console.log(result);
        this.id = result.message.role;
      })
    },
  }
}
</script>

<style>
.el-badge__content.is-fixed{
  top:31px;
  right:0;
}
</style>



