<template>
  <div class="foot" style="text-align: center">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      layout="total, prev, pager, next"
      :total="total">
    </el-pagination>
  </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  methods: {
    handleSizeChange(val) {
      console.log(val);
    },
    handleCurrentChange(val) {
      this.getPageOrder(val)
      .then( result => {
        let list = result.message.list;
        this.pageOrderSay(list);
      })
    },
    pageOrderSay(data) {
      this.$emit('say', data)
    },
    getPageOrder(val) {
      let url =  this.url+val
      let form = this.form
      try{
        if( !(this.search.itemNumber == "" && this.search.name == "" && this.search.orderNo == "" && this.search.userId == "") ||
            !(this.search.mergeNumber == "" && this.search.name == "" && this.search.status == "" && this.search.userId == "") ||
            !(this.search.name == "" && this.search.status == "" && this.search.userId == "")
        ) {
          form = this.search
        }
      }
      catch(e){
        console.log('出错')
      }

      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            data: JSON.stringify(form),
      			url,
      			headers: {
      				'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
              'Access-Control-Allow-Origin': '*'
            },
      			contentType: "application/json; charset=utf-8",
      			dataType: "JSON",
      			success(result) {
      				resolve(result);
      			},
      			error(callback) {
      				reject(callback);
      			}
          })
      })
    },
  },

  data() {
    return {
      currentPage: 1,

    };
  },
  props: {
    total: {
      type: Number,
      default() {
        return Number
      }
    },
    pageSize: {
      type: Number,
      default: 0
    },
    url: {
      type: String,
      default() {
        return String
      }
    },
    form: {
      type: Object,
      default: () => {}
    },
    search: {
      type: Object,
      default: () => {}
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.pagination{
  position: relative;
  margin:0 auto;
  text-align: center;
}

</style>



